.intro {
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;

  color: white;

  // width: 450px;
  max-width: 95%;
  max-height: 95%;
  padding: 0px 40px;
  box-sizing: border-box;
  border-radius: 16px;
  margin-left: 10vw;
  margin-right: 10vw;

  footer {
    width: 100%;
    margin-top: 16px;

    button {
      background-color: #446fa0;

      margin: auto;
      padding: 16px;
      border-radius: 8px;

      &:hover,
      &:focus-within {
        background-color: cornflowerblue;
      }
    }
  }
}
